import './App.css';
import React from 'react';
import ExpandingButton from './ExpandingButton.js';

class Number extends React.Component {
    render() {
        return(
            <div className="big-number-container">
                <div className="big-number">
                    {this.props.number}
                </div>
                <div className="big-number-description">
                    {this.props.description}
                </div>
            </div>
        );
    }
}

class Numbers extends React.Component {
    render() {
        let numbers = "";
        if (this.props.numbers) {
            numbers = this.props.numbers.map(e =>
                <div className="number-row" key={e}>
                    {e.map(ee =>
                        <Number key={ee} number={ee.number} description={ee.description} />
                    )}
                </div>
            );
        }

        return (
            <div className="numbers">
                {numbers}
            </div>
        );
    }
}

class Project extends React.Component {
    render() {
        const data = this.props.content;
        const buttonDiv = !data.button ? "" :
            <div className="button-wrapper">
                <ExpandingButton text={data.button.text} link={data.button.href}/>
            </div>;

        return (
            <div className="project">
                <span className="title">
                    {data.title}
                </span>
                <div className="header">
                    <span className="flavor">
                        {data.flavor}
                    </span>
                    <span className="date">
                        {data.date}
                    </span>
                </div>
                <img src={data.img}/>
                <div className="description">
                    {data.description}
                </div>
                <Numbers numbers = {data.numbers} />
                {buttonDiv}
            </div>
        );
    }
}

class Projects extends React.Component {
    render() {
        const data = this.props.content;
        const bgImg = {
            backgroundImage: `linear-gradient( rgba(0, 0, 0, ${data.darken}), rgba(0, 0, 0, ${data.darken}) ), url(\"${data.bgImg}\")`
        };

        let projectList = [];
        if (data.projectList) {
            projectList = data.projectList.map(e => <Project key={e} content={e}/>);
        }

        return (
            <div className="project-page" style={bgImg}>
                <div className="header">
                    <div className="title">
                        {data.title}
                    </div>
                    <div className="flavor">
                        {data.flavor}
                    </div>
                </div>
                <div className="projects">
                    {projectList}
                </div>
            </div>
        );
    }
}

export default Projects;